import { SidebarContainer, SidebarMenu, SidebarLink, SideBtnWrap, SignPanelBtn, SideMemberDiv, SideMemberImgDiv, MemberHello } from './SidebarElements'
import { useSelector } from 'react-redux'
import { signOut } from '../../utils/firebase.js'

const SideBar = ({ isOpenSideBar, toggleSideBar, toggleSignPanel }) => {
  const user = useSelector((store) => store.user.currentUser)
  return (
    <SidebarContainer isOpenSideBar={isOpenSideBar}>
        <SidebarMenu>
          {user ?
            <SideMemberDiv>
              <SideMemberImgDiv>
                <img alt='' src={user.photoURL ? user.photoURL : '/default-user-image.png'}/>
              </SideMemberImgDiv>
              <MemberHello>{user.displayName}</MemberHello>
            </SideMemberDiv> : ''}
          <SidebarLink to="/" onClick={toggleSideBar}></SidebarLink>
          <SidebarLink to="/blog" onClick={toggleSideBar}></SidebarLink>
          {user ? <SidebarLink to="/new-song" onClick={toggleSideBar}></SidebarLink> : ''}
          {user ? <SidebarLink to="/new-post" onClick={toggleSideBar}></SidebarLink> : ''}
          {user ? <SidebarLink to="/member/collections/songs" onClick={toggleSideBar}></SidebarLink> : ''}
          {user ? <SidebarLink to="/member/personal-info" onClick={toggleSideBar}></SidebarLink> : ''}
        </SidebarMenu>
        <SideBtnWrap>
        {user ?  <SignPanelBtn onClick={() => {toggleSideBar(); signOut()}}></SignPanelBtn> : <SignPanelBtn onClick={() => {toggleSideBar(); toggleSignPanel()}}> / </SignPanelBtn>}
        </SideBtnWrap>
    </SidebarContainer>
  )
}

export default SideBar;
