import { useState, useEffect } from 'react'
import {
  CommentsContainer,
  CommentAddSection,
  Notice,
  CommentAddArea,
  CommentInfo,
  CommentorImgDiv,
  CommentorName,
  CommentTextArea,
  CommentButtons,
  SubmitButton,
} from './CommentAreaElements.js'
import Comment from '../Comment'
import { useSelector } from 'react-redux'
import { addNewComment, getCommentsOnSnapshot } from '../../utils/firebase.js'

const CommentArea = ({ area, id }) => {
  const user = useSelector((store) => store.user.currentUser)
  const [commentInput, setCommentInput] = useState('')
  const [comments, setComments] = useState([])

  function handleCommentInputSubmit(){
    addNewComment(area, id, commentInput, user.uid).then(() => {
      setCommentInput('')
    })
  }

  useEffect(() => {
    getCommentsOnSnapshot(area, id, setComments)
  },[area, id])

  return (
    <CommentsContainer>
      <CommentAddSection>
        <h3></h3>
        {user ?
          <CommentAddArea>
            <CommentInfo>
              <CommentorImgDiv><img alt='' src={user.photoURL ? user.photoURL : '/default-user-image.png'}/></CommentorImgDiv>
              <CommentorName>{user.displayName}</CommentorName>
            </CommentInfo>
            <CommentTextArea placeholder='...' value={commentInput} onChange={(e)=> setCommentInput(e.target.value)}>
            </CommentTextArea>
            <CommentButtons>
              <SubmitButton onClick={handleCommentInputSubmit}></SubmitButton>
            </CommentButtons>
          </CommentAddArea>
          :
          <Notice><span></span></Notice>
        }
      </CommentAddSection>
      {comments.map((comment) => {
        return (
          <Comment key={comment.id} currentComment={comment}>
          </Comment>
        )
      })}

    </CommentsContainer>
  )
}

export default CommentArea
