import { useState } from 'react'
import {
  SignPanelTitle,
  SignPanelTitleDesc,
  SignPanelButton,
  SignPanelInput,
  LinkGroups,
  SignPanelChange,
  Warning
} from './SignPanelElements.js'
import { forgetPassword } from '../../utils/firebase.js'

const ForgetPassword = ({ setPanelStatus, toggleSignPanel }) => {
  const [email, setEmail] = useState('')
  const [message, setMessage] = useState('')

  function handleSubmit(){
    forgetPassword(email)
      .then(() => {
        setEmail('')
        setMessage('')
      })
      .catch((err) => {
        setEmail('')
        setMessage('')
      })
  }
  return (
    <>
      <SignPanelTitle></SignPanelTitle>
      {message && <Warning>{message}</Warning>}
      <SignPanelTitleDesc></SignPanelTitleDesc>
      <SignPanelInput placeholder=' email' type='email' value={email} onChange={(e) => {setEmail(e.target.value)}}></SignPanelInput>
      <SignPanelButton onClick={(e) => {handleSubmit(e)}}></SignPanelButton>
      <LinkGroups>
        <SignPanelChange onClick={() => {setPanelStatus('signup')}}></SignPanelChange>
        <SignPanelChange onClick={() => {setPanelStatus('signin')}}></SignPanelChange>
      </LinkGroups>
    </>
  )
}

export default ForgetPassword
