import { useState, useEffect } from 'react'
import { faPen } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useSelector } from 'react-redux'
import { getUserInfoOnSnapshot, updatePassword, updateDisplayName, updateIntroduction, updateImage } from '../../utils/firebase.js'
import 'firebase/compat/auth'
import "firebase/compat/firestore"
import "firebase/compat/storage"
import {
  MemberPageContainer,
  TitleButtonsGroup,
  TitleNormalButton,
  TitleActiveButton,
  TitleDiv,
  PersonalInfoContainer,
  InfoSection,
  InfoTitle,
  InfoElementTitle,
  InfoElementMainDiv,
  ImgDiv,
  EditButton,
  Input,
  IntroductionArea,
  IntroductionDiv,
  ModalBackground,
  ModalPanel,
  ModalInnerTop,
  ModalInnerDiv,
  ModalInnerBottom,
  ModalButton,
  ModalImgDiv,
  ModalUploadButton,
  ModalTextArea,
  Warning,
} from './PersonalInfoElements.js'
import Loading from '../../components/Loading'

const PersonalInfo = () => {
  const [isLoading, setIsLoading] = useState(false)
  const [errorMessage, setErrorMessage] = useState('')
  const [modalIsOpen, setModalIsOpen] = useState(false)
  const [mode, setMode] = useState(0)
  const [introduction, setIntroduction] = useState('')
  const [displayName, setDisplayName] = useState('')
  const [file, setFile] = useState(null)
  const [currentPassword, setCurrentPassword] = useState('')
  const [newPassword, setNewPassword] = useState('')
  const [newPasswordConfirm, setNewPasswordConfirm] = useState('')
  const [shownInfo, setShownInfo] = useState({})
  const previewUrl = file ? URL.createObjectURL(file) : '/image.png'
  const user = useSelector((store) => store.user.currentUser)

  function handleUpdateAvatar() {
    function handleUpdateSucceed(){
      setModalIsOpen(false)
      setFile(null)
      setIsLoading(false)
    }
    setIsLoading(true)
    updateImage(user.uid, file, handleUpdateSucceed, setErrorMessage)
  }

  function handleEditIntroduction() {
    if(introduction === '') {
      setErrorMessage('')
      return
    }
    function handleUpdateSucceed(){
      setModalIsOpen(false)
      setIntroduction(null)
      setIsLoading(false)
    }
    setIsLoading(true)
    updateIntroduction(user.uid, introduction, handleUpdateSucceed, setErrorMessage)
  }

  function handleEditDisplayName() {
    if(displayName === '') {
      setErrorMessage('')
      return
    }
    function handleUpdateSucceed(){
      setModalIsOpen(false)
      setDisplayName(null)
      setIsLoading(false)
    }
    setIsLoading(true)
    updateDisplayName(user.uid, displayName, handleUpdateSucceed, setErrorMessage)
  }

  function handleChangePssword(){
    if(!newPassword || !newPasswordConfirm || !currentPassword){
      setErrorMessage('')
      return
    }
    if(newPassword !== newPasswordConfirm) {
      setErrorMessage('')
      return
    }

    function handleUpdateSucceed(){
      setModalIsOpen(false)
      setIsLoading(false)
      setCurrentPassword('')
      setNewPassword('')
      setNewPasswordConfirm('')
      alert('')
    }
    setIsLoading(true)
    updatePassword(user.email, currentPassword, newPassword, handleUpdateSucceed, setErrorMessage)
  }

  useEffect(() => {
    getUserInfoOnSnapshot(user.uid, setShownInfo)
  },[])

  return (
      <MemberPageContainer>
        {isLoading && <Loading />}
        <TitleButtonsGroup>
          <TitleActiveButton></TitleActiveButton>
          <TitleNormalButton to='/member/myposts'></TitleNormalButton>
        </TitleButtonsGroup>
        <PersonalInfoContainer>
          <TitleDiv><InfoTitle></InfoTitle></TitleDiv>
          <InfoSection>
            <InfoElementTitle></InfoElementTitle>
            <InfoElementMainDiv>
              <ImgDiv><img alt='' src={shownInfo.photoURL ? shownInfo.photoURL : '/default-user-image.png'}/></ImgDiv>
              <EditButton
                onClick={() => {setModalIsOpen(!modalIsOpen);setMode(1)}}>
                <FontAwesomeIcon size='1x' icon={faPen}/>
              </EditButton>
            </InfoElementMainDiv>
          </InfoSection>
          <InfoSection>
            <InfoElementTitle></InfoElementTitle>
            <InfoElementMainDiv>
              <Input disabled value={shownInfo.email}/>
            </InfoElementMainDiv>
          </InfoSection>
          <InfoSection>
            <InfoElementTitle></InfoElementTitle>
            <InfoElementMainDiv>
              <Input disabled value={'******'}/>
              <EditButton onClick={() => {setModalIsOpen(!modalIsOpen);setMode(2)}}><FontAwesomeIcon size='1x' icon={faPen}/></EditButton>
            </InfoElementMainDiv>
          </InfoSection>
          <InfoSection>
            <InfoElementTitle></InfoElementTitle>
            <InfoElementMainDiv>
              <Input disabled value={shownInfo.displayName}/>
              <EditButton onClick={() => {setModalIsOpen(!modalIsOpen);setMode(3)}}><FontAwesomeIcon size='1x' icon={faPen}/></EditButton>
            </InfoElementMainDiv>
          </InfoSection>
          <InfoSection>
            <InfoElementTitle></InfoElementTitle>
            <IntroductionDiv>
              <IntroductionArea>{shownInfo.introduction ? shownInfo.introduction : ''}</IntroductionArea>
              <EditButton onClick={() => {setModalIsOpen(!modalIsOpen);setMode(4)}}><FontAwesomeIcon size='1x' icon={faPen}/></EditButton>
            </IntroductionDiv>
          </InfoSection>
          {modalIsOpen &&
            <ModalBackground>
              {mode === 1 &&
                <ModalPanel>
                  <InfoTitle></InfoTitle>
                  <ModalInnerTop>
                    <ModalImgDiv><img alt='' src={previewUrl}/></ModalImgDiv>
                    <ModalUploadButton htmlFor='post-image'></ModalUploadButton>
                    <input
                    type="file"
                    id='post-image'
                    style={{display: 'none'}}
                    onChange={(e) => setFile(e.target.files[0])}/>
                  </ModalInnerTop>
                  <ModalInnerBottom>
                    <ModalButton onClick={() => {handleUpdateAvatar()}}></ModalButton>
                    <ModalButton onClick={() => {setModalIsOpen(!modalIsOpen);setMode(0)}}></ModalButton>
                  </ModalInnerBottom>
                </ModalPanel>
              }
              {mode === 2 &&
                <ModalPanel>
                  <InfoTitle></InfoTitle>
                  <ModalInnerTop>
                  {errorMessage && <Warning>{errorMessage}</Warning>}
                  <ModalInnerDiv>
                    <InfoElementTitle></InfoElementTitle>
                    <Input value={currentPassword} onChange={(e) => setCurrentPassword(e.target.value)}/>
                  </ModalInnerDiv>
                    <ModalInnerDiv>
                      <InfoElementTitle></InfoElementTitle>
                      <Input value={newPassword} onChange={(e) => setNewPassword(e.target.value)}/>
                    </ModalInnerDiv>
                    <ModalInnerDiv>
                      <InfoElementTitle></InfoElementTitle>
                      <Input value={newPasswordConfirm} onChange={(e) => setNewPasswordConfirm(e.target.value)}/>
                    </ModalInnerDiv>
                  </ModalInnerTop>
                  <ModalInnerBottom>
                    <ModalButton onClick={handleChangePssword}></ModalButton>
                    <ModalButton onClick={() => {setModalIsOpen(!modalIsOpen);setMode(0)}}></ModalButton>
                  </ModalInnerBottom>
                </ModalPanel>
              }
              {mode === 3 &&
                <ModalPanel>
                  <InfoTitle></InfoTitle>
                  {errorMessage && <Warning>{errorMessage}</Warning>}
                  <ModalInnerTop>
                    <ModalInnerDiv>
                      <InfoElementTitle></InfoElementTitle>
                      <Input placeholder='' value={displayName} onChange={(e) => setDisplayName(e.target.value)}/>
                    </ModalInnerDiv>
                  </ModalInnerTop>
                  <ModalInnerBottom>
                    <ModalButton onClick={handleEditDisplayName}></ModalButton>
                    <ModalButton onClick={() => {setModalIsOpen(!modalIsOpen);setMode(0)}}></ModalButton>
                  </ModalInnerBottom>
                </ModalPanel>
              }
              {mode === 4 &&
                <ModalPanel>
                  <InfoTitle></InfoTitle>
                  {errorMessage && <Warning>{errorMessage}</Warning>}
                  <ModalInnerTop>
                    <ModalInnerDiv>
                      <InfoElementTitle></InfoElementTitle>
                      <ModalTextArea value={introduction} onChange={(e) => {setIntroduction(e.target.value)}}/>
                    </ModalInnerDiv>
                  </ModalInnerTop>
                  <ModalInnerBottom>
                    <ModalButton onClick={handleEditIntroduction}></ModalButton>
                    <ModalButton onClick={() => {setModalIsOpen(!modalIsOpen);setMode(0)}}></ModalButton>
                  </ModalInnerBottom>
                </ModalPanel>
              }
            </ModalBackground>
          }
        </PersonalInfoContainer>
      </MemberPageContainer>
  )
}

export default PersonalInfo
