import { useState } from 'react'
import {
  SignPanelTitle,
  SignPanelTitleDesc,
  SignPanelButton,
  SignPanelInput,
  LinkGroups,
  SignPanelChange,
  Warning
} from './SignPanelElements.js'
import { signIn } from '../../utils/firebase.js'

const SignIn = ({ setPanelStatus, toggleSignPanel }) => {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [errorMessage, setErrorMessage] = useState('')
  function signInSucceed(){
    toggleSignPanel()
    setEmail('')
    setPassword('')
  }
  function handleSubmit(){
    signIn(email, password, signInSucceed, setErrorMessage)
  }
  return (
    <>
      <SignPanelTitle></SignPanelTitle>
      {errorMessage && <Warning>{errorMessage}</Warning>}
      <SignPanelTitleDesc></SignPanelTitleDesc>
      <SignPanelInput type='email' placeholder=' email' value={email} onChange={(e) => {setEmail(e.target.value)}}></SignPanelInput>
      <SignPanelTitleDesc></SignPanelTitleDesc>
      <SignPanelInput type='password'  value={password} onChange={(e) => {setPassword(e.target.value)}}></SignPanelInput>
      <SignPanelButton onClick={(e) => {handleSubmit(e)}}></SignPanelButton>
      <LinkGroups>
        <SignPanelChange onClick={() => {setPanelStatus('signup')}}></SignPanelChange>
        <SignPanelChange onClick={() => {setPanelStatus('forget');console.log('yoyo')}}></SignPanelChange>
      </LinkGroups>
    </>
  )
}

export default SignIn
