import React, { useState, useEffect, useRef } from 'react'
import { FaBars } from 'react-icons/fa'
import {
  Nav,
  NavbarContainer,
  NavLogo,
  MobileIcon,
  NavMenu,
  NavItem,
  NavLinks,
  NavBtn,
  NavBtnLink,
  NavMemberImgDiv,
  DropDownContainer,
  DropDownMain,
  DropDownItem,
  DropDownBottom,
  MemberDiv
} from './HeaderElements'
import { useSelector } from 'react-redux'
import { signOut as fireSignOut } from '../../utils/firebase.js'

const Header = ({ toggleSignPanel, toggleSideBar }) => {
  const user = useSelector((store) => store.user.currentUser)
  const [isOpenDropDown, setIsOpenDropDown] = useState(false)
  const dropDownRef = useRef(null)

  const signOut = () => {
    fireSignOut()
    setIsOpenDropDown(false)
  }

  useEffect(() => {
    function handleClickOutside(e) {
      if (dropDownRef.current && !dropDownRef.current.contains(e.target)) {
        setIsOpenDropDown(false);
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [dropDownRef]);
  return (
    <Nav>
      <NavbarContainer>
        <NavLogo to="/">Indielog</NavLogo>
        <MobileIcon onClick={toggleSideBar}>
          <FaBars />
        </MobileIcon>
        <NavMenu>
          <NavItem>
            <NavLinks to="/"></NavLinks>
          </NavItem>
          <div style={{color: '#fff'}}> | </div>
          <NavItem>
            <NavLinks to="/blog"></NavLinks>
          </NavItem>
        </NavMenu>
        <NavBtn>
        {user ?
          <MemberDiv ref={dropDownRef}>
            <DropDownContainer  isOpenDropDown={isOpenDropDown}>
              <DropDownMain isOpenDropDown={isOpenDropDown}>
                <DropDownItem to='/new-song'></DropDownItem>
                <DropDownItem to='/new-post'></DropDownItem>
                <DropDownItem to='/member/collections/songs'></DropDownItem>
                <DropDownItem to='/member/personal-info'></DropDownItem>
              </DropDownMain>
              <DropDownBottom isOpenDropDown={isOpenDropDown} onClick={signOut}></DropDownBottom>
            </DropDownContainer>
            <NavMemberImgDiv onClick = {() => setIsOpenDropDown(!isOpenDropDown)}>
              <img alt='' src={user.photoURL ? user.photoURL : '/default-user-image.png'}/>
            </NavMemberImgDiv>
          </MemberDiv>
          :
          <NavBtnLink onClick={toggleSignPanel}> / </NavBtnLink>
        }
        </NavBtn>
      </NavbarContainer>
    </Nav>
  )
}

export default Header
