import { useState } from 'react'
import {
  SignPanelTitle,
  SignPanelTitleDesc,
  SignPanelButton,
  SignPanelInput,
  LinkGroups,
  SignPanelChange,
  Warning
} from './SignPanelElements.js'
import { signUp } from '../../utils/firebase.js'
const SignUp = ({ setPanelStatus, toggleSignPanel }) => {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [passwordConfirm, setPasswordConfirm] = useState('')
  const [errorMessage, setErrorMessage] = useState('')

  function handleSignUpSucceed(){
    toggleSignPanel()
    setEmail('')
    setPassword('')
  }
  function handleSubmit(){
    if(passwordConfirm !== password) {
      setErrorMessage('')
      return
    }
    signUp(email, password, handleSignUpSucceed, setErrorMessage)
  }
  return (
    <>
      <SignPanelTitle></SignPanelTitle>
      {errorMessage && <Warning>{errorMessage}</Warning>}
      <SignPanelTitleDesc></SignPanelTitleDesc>
      <SignPanelInput type='email' placeholder=' email' value={email} onChange={(e) => {setEmail(e.target.value)}}></SignPanelInput>
      <SignPanelTitleDesc></SignPanelTitleDesc>
      <SignPanelInput type='password' value={password} onChange={(e) => {setPassword(e.target.value)}}></SignPanelInput>
      <SignPanelTitleDesc></SignPanelTitleDesc>
      <SignPanelInput type='password' value={passwordConfirm} onChange={(e) => {setPasswordConfirm(e.target.value)}}></SignPanelInput>
      <SignPanelButton onClick={handleSubmit}></SignPanelButton>
      <LinkGroups>
        <SignPanelChange onClick={() => {setPanelStatus('signin')}}></SignPanelChange>
      </LinkGroups>
    </>
  )
}

export default SignUp
